import mongoose from 'mongoose'
const Schema = mongoose.Schema

const CommentsSchema = new Schema(
  {
    kind: { type: String },
    contentId: { type: String, required: true }, // 콘텐츠의 id
    text: { type: String, required: true }, // 댓글 내용
    userid: { type: Schema.Types.ObjectId, ref: 'portfolioUser' },
  },
  {
    toObject: { virtuals: true },
    toJSON: { virtuals: true },
    versionKey: false,
    timestamps: true,
  }
)

export const Comments = mongoose.model('portfolioComments', CommentsSchema)
